<template>
  <ion-page>
    <ion-header :translucent="true">
      <ion-toolbar style="padding: 10px;">
        <ion-img slot="start" style="height: 60px;" src="assets/Malawi-Coat_of_arms_of_arms.png"></ion-img>
        <ion-title style="text-transform: uppercase;" class="his-lg-text">IBLIS Reception</ion-title>
      </ion-toolbar>
    </ion-header>
    <ion-content :fullscreen="true">
      <ion-card style="margin:10px;">
        <barcode-input placeholder="Scan Accession Number" @onScan="onScan">
        </barcode-input>
      </ion-card>
      <ion-card cstyle="margin:10px auto;margin-top: 3%;width:90%">
        <ion-list>
          <ion-item lines="none" class="ion-padding his-lg-text">
            <ion-icon :icon="person" size="large" color="black" slot="start"></ion-icon>
            <ion-label>Full name</ion-label>
            <ion-label slot="end"><b>{{ fullName }}</b></ion-label>
          </ion-item>
          <ion-item lines="none" class="ion-padding his-lg-text">
            <ion-icon :icon="home" size="large" color="black" slot="start"></ion-icon>
            <ion-label>Location</ion-label>
            <ion-label slot="end"><b>{{ getLocation() }}</b></ion-label>
          </ion-item>
          <ion-item lines="none" class="ion-padding his-lg-text">
            <ion-icon :icon="medkit" size="large" color="black" slot="start"></ion-icon>
            <ion-label>Department</ion-label>
            <ion-label slot="end"><b>{{ department }}</b></ion-label>
          </ion-item>
        </ion-list>
      </ion-card>
    </ion-content>
    <ion-footer>
      <ion-toolbar color="light" style="padding: 10px;">
        <ion-button @click="signout" size="large" color="danger">
          Logout
        </ion-button>
        <ion-button @click="() => router.push('/tests')" slot="end" size="large">
          Tests
        </ion-button>
        <ion-button @click="router.push('/find')" slot="end" size="large" color="success">
          Find/Register
        </ion-button>
      </ion-toolbar>
    </ion-footer>
    <div id="rootContainer"></div>
  </ion-page>
</template>

<script setup lang="ts">
import {
  IonContent,
  IonButton,
  IonHeader,
  IonPage,
  IonTitle,
  IonToolbar,
  IonFooter,
  IonList,
  IonItem,
  IonLabel,
  IonIcon,
  IonCard,
  IonImg
} from '@ionic/vue';
import { person, home, medkit } from "ionicons/icons";
import { computed, ref } from 'vue';
import { Client, ClientError } from "@/composables/client"
import BarcodeInput from "@/components/BarcodeInput.vue"
import router from "@/router"
import Orders from "@/composables/orders";
import { PatientDialog } from "@/utils/PatientDialog";
import { toastDanger, toastSuccess, toastWarning } from '@/utils/Alerts';
import PrintService from "@/services/print_service";
import { LabLocation } from 'types';

const { signout, token, getLocation } = Client()

const details = ref<any>();
const data = token.value?.authorization?.user;
const modalOpen = ref<boolean>(false);

const handleModal = () => {
  modalOpen.value = true;
  PatientDialog(modalOpen.value, details.value);
};


const fullName = computed(() => {
  return `${data?.first_name || ''} ${data?.last_name || ''}`
})

const department = computed(() => {
  return `${(data?.departments || []).map((d: any) => d.name).splice(0, 3).join(', ')}...`
});

const checkTrackingNumber = (trackingNumber: string): boolean => {
  const regex = /^(x|l).*\d/;
  return regex.test(trackingNumber.toLowerCase());
}

const checkOERRNumber = (trackingNumber: string): boolean => {
  return trackingNumber.includes('^');
}

const localSearch = async(accessioNumber: string): Promise<void> => {
  const client = Client()
  const res = await client.getJson('global');
  const siteCode = res?.data.code
  const param = `${siteCode}${accessioNumber}`
  Orders().searchbyID(param)
}

const createOrderFromOERR= async (text: string) => {
  const printService = new PrintService();
  const client = Client();
  const { getLocation, token } = Client();
  const userData = token.value?.authorization?.user;
  const labLocationID = userData.lab_locations.filter(
    (location: LabLocation) => location.name == getLocation()
  );
  const res = await client.postJson('orders', { "oerr": true, "segments": text, "lab_location": labLocationID[0].id })
  .then((res: any) => {
      details.value = res.data;
      const accession_number = res.data.accession_number;
      if (!res.ok) return toastWarning(res.error);
      const params: Record<string, any> = {
        "accession_number": accession_number,
        "oerr": true,
      }
      printService.writeLbl(
        "printout/accession_number",
        params,
        `${accession_number}.lbl`
      );

      modalOpen.value = true;

      try {
        router.push(`/tests`);
      } catch (error) {
        console.log(error);
      }

      toastSuccess("Order as been created!");
      handleModal();
    })
    .catch(() => toastDanger(ClientError.GENERAL_ERROR));
  if(res){
    router.push('/tests')
  }
}

const onScan = async (text: string) => {
  if(checkOERRNumber(text)){
    createOrderFromOERR(text)
  }else if(checkTrackingNumber(text)){
    checkTrackingNumber(text) ? router.push(`/nlims-order?tracking-number=${text}`) : localSearch(text)
  }
}
</script>
