<template>
  <ion-page>
    <ion-content class="ion-padding">
      <ion-modal :is-open="open">
        <ion-header>
          <ion-toolbar>
            <ion-title style="font-size: 20px; font-weight: 600"
              >Patient order</ion-title
            >
            <ion-buttons slot="end">
              <ion-button :strong="true" @click="handleDialog()">
                <IonIcon size="large" :icon="close"></IonIcon>
              </ion-button>
            </ion-buttons>
          </ion-toolbar>
        </ion-header>
        <ion-content>
          <ion-list>
            <ion-item>
              <ion-label style="font-size: 18px; font-weight: 600"
                >Tracking Number</ion-label
              >
              <ion-label slot="end">{{ order?.tracking_number }}</ion-label>
            </ion-item>
            <ion-item>
              <ion-label style="font-size: 18px; font-weight: 600"
                >Accession Number</ion-label
              >
              <ion-label slot="end">{{ order?.accession_number }}</ion-label>
            </ion-item>
            <ion-item>
              <ion-label style="font-size: 18px; font-weight: 600"
                >Patient Name</ion-label
              >
              <ion-label slot="end">
                {{
                  `${order?.client.first_name} ${
                    order?.client.middle_name != null ? order?.client.middle_name : ""
                  } ${order?.client.last_name}`
                }}
              </ion-label>
            </ion-item>
            <ion-item>
              <ion-label style="font-size: 18px; font-weight: 600"
                >Specimen Type</ion-label
              >
              <ion-label slot="end">{{ order?.specimen }}</ion-label>
            </ion-item>
            <ion-item>
              <ion-label style="font-size: 18px; font-weight: 600"
                >Test(s) Ordered</ion-label
              >
              <ion-label slot="end">{{
                extractedTests(order?.tests)
              }}</ion-label>
            </ion-item>
            <ion-item>
              <ion-label style="font-size: 18px; font-weight: 600"
                >Specimen Status</ion-label
              >
              <ion-label slot="end">{{
                order?.order_status == "specimen-not-collected"
                  ? "Specimen not collected"
                  : order?.order_status
              }}</ion-label>
            </ion-item>
          </ion-list>
        </ion-content>
        <ion-footer>
          <ion-toolbar color="light" style="padding: 5px">
            <ion-button
              @click="changeSpecimenStatus('accepted')"
              slot="end"
              size="large"
              color="success"
            >
              Accept
            </ion-button>
            <ion-button
              @click="rejectOrder"
              slot="end"
              size="large"
              color="danger"
            >
              Reject
            </ion-button>
            <ion-button @click="addTest" slot="end" size="large" color="primary">
              Add Test
            </ion-button>
            <ion-button
              @click="addOrderToEncounter(details.encounter_id)"
              slot="end"
              size="large"
              color="success"
            >
              Add Order
            </ion-button>
          </ion-toolbar>
        </ion-footer>
      </ion-modal>
    </ion-content>
  </ion-page>
</template>

<script lang="ts">
import { Client } from "@/composables/client";
import router from "@/router";
import { toastSuccess, toastWarning } from "@/utils/Alerts";
import { MultiStepPopupForm } from "@/utils/PopupKeyboard";
import {
  IonButtons,
  IonButton,
  IonIcon,
  IonModal,
  IonHeader,
  IonContent,
  IonToolbar,
  IonTitle,
  IonItem,
  IonLabel,
  IonFooter,
  IonList,
  IonPage,
  modalController
} from "@ionic/vue";

import { close } from "ionicons/icons";

import { PropType } from "vue";

import { defineComponent } from "vue";
import { FieldType } from "../Forms/BaseFormElements";
import { Option } from "../Forms/FieldInterface";
import Validation from "@/components/Forms/validations/StandardValidations";
import Orders from "@/composables/orders";
import { LabLocation } from "types";
import PrintService from "@/services/print_service";

export default defineComponent({
  components: {
    IonPage,
    IonButtons,
    IonButton,
    IonIcon,
    IonModal,
    IonHeader,
    IonContent,
    IonToolbar,
    IonTitle,
    IonFooter,
    IonItem,
    IonLabel,
    IonList,
  },
  data(): any {
    return {
      open: true as boolean,
      close: close,
      details: {
        client: {
          date_of_birth: "",
          first_name: "",
          id: "",
          last_name: "",
          middle_name: "",
          sex: "",
        },
      } as any,
    };
  },
  props: {
    order: {
      required: true,
      type: Object as PropType<any>,
    },
    show: {
      required: true,
      default: false,
      type: Boolean as PropType<any>,
    },
  },
  async created() {
    const client = Client();
    const res = await client.getJson(`tests/${this.order.tests[0].id}`);
    if (res?.ok) {
      this.details = res?.data;
    }
  },
  methods: {
    extractedTests(tests: Array<any>): string {
      return tests.map((test) => test.test_type_name).join(", ");
    },
    handleDialog(): void {
      this.open = false;
    },
    async changeSpecimenStatus(status: string): Promise<void> {
      const client = Client();

      const res = await client.putJson(`order_statuses/${status}`, {
        order_id: this.details.order_id,
      });

      if (res?.ok) {
        toastSuccess(`Specimen ${status} successfully!`);
        this.open = false;
        router.push(`/tests`);
      }
    },
    async rejectOrder(){
      this.open = false;
      router.push(`order/reject/${this.order.id}`);
    },
    loadOrder(accessionNumber: string) {
      Orders().searchbyID(accessionNumber);
    },
    async addTest() {
      this.handleDialog();
      const client = Client();
      MultiStepPopupForm(
        [
          {
            id: "test_types",
            helpText: "Select test types",
            type: FieldType.TT_MULTIPLE_SELECT,
            config: {
              showKeyboard: true,
            },
            computedValue: (val: Option[], f: any) => {
              return {
                order_id: this.details.order_id,
                tests: val.map((v: { value: any; label: any }) => {
                  return {
                    specimen: v.value,
                    test_type: v.label,
                  };
                }),
              };
            },
            options: async () => {
              const res = await client.getJson("specimen/test_types", {
                specimen_id: this.details.specimen_id as number,
              });

              console.log(res);
              if (!res?.ok) return [];
              return res.data.map((testType: any) => ({
                label: testType,
                value: this.details.specimen_id,
              }));
            },
            validation: (val: Option) => Validation.required(val),
          },
        ],
        async (_: any, payload: any) => {
          const req = await client.postJson(
            "orders/add_test_to_order",
            payload.test_types
          );
          if (!req?.ok) return toastWarning(req?.error);
          modalController.getTop().then((v) => v && modalController.dismiss());
          this.loadOrder(req.data?.accession_number);
        },
        null
      );
    },
    async addOrderToEncounter(encounterId: number) {
      this.handleDialog();
      const client = Client();
      const patientSex = this.details.client?.sex.match(/f/i)
        ? "Female"
        : "Male";
      let options: Option[] = [];
      let testTypes: Option[] = [];
      let specimenID: number | null = null;
      MultiStepPopupForm(
        [
          {
            id: "specimen_type",
            helpText: "Specimen type",
            type: FieldType.TT_SELECT,
            requireNext: false,
            init: async () => {
              const res = await client.getJson("specimen");
              if (!res?.ok) return false;
              options = res.data.map((data: any) => ({
                label: data.name,
                value: data.id,
              }));
              return true;
            },
            config: {
              showKeyboard: true,
            },
            options: () => options,
            validation: (val: Option) => Validation.required(val),
          },
          {
            id: "test_types",
            helpText: "Select test types",
            type: FieldType.TT_MULTIPLE_SELECT,
            config: {
              showKeyboard: true,
            },
            computedValue: (val: Option[], f: any) => {
              return {
                tests: val.map((v: Option) => {
                  return {
                    specimen: f.specimen_type.value,
                    test_type: v.value,
                  };
                }),
              };
            },
            options: async (f: any) => {
              if (f.specimen_type.value) {
                testTypes = [];
                specimenID = f.specimen_type.value;
                const res: any = await client.getJson("specimen/test_types", {
                  specimen_id: specimenID as number,
                  sex: patientSex,
                });
                if (!res?.ok) return [];
                testTypes = res.data.map((testType: string) => ({
                  label: testType,
                  value: testType,
                }));
              }
              return testTypes;
            },
            validation: (val: Option) => Validation.required(val),
          },
        ],
        async (_: any, payload: any) => {
          payload.encounter_id = encounterId;
          const { getLocation, token } = Client();
          const userData = token.value?.authorization?.user;
          const labLocation = userData.lab_locations.find(
            (location: LabLocation) => location.name == getLocation()
          );
          const req = await client.postJson("orders/add_order_to_encounter", {
            encounter_id: payload.encounter_id,
            lab_location: labLocation.id || 0,
            tests: payload.test_types.tests,
          });
          if (!req?.ok) return toastWarning(req?.error);
          if(req.ok) this.printAccessNumber(req.data.accession_number);
          modalController.getTop().then((v) => v && modalController.dismiss());
          this.loadOrder(req.data.accession_number);
        },
        null
      );
    },
    printAccessNumber(accessionNumber: string) {
      const printService = new PrintService();
      const accession_number = accessionNumber;
      printService.writeLbl(
        "printout/accession_number",
        { accession_number },
        `${accession_number}.lbl`
      );
    },
  },
});
</script>
<style lang=""></style>
